<template>
  <div class="all">
    <div class="pubhead">发布</div>
    <div class="pubtitle">
      <input type="text" v-model="title" placeholder="请输入标题" />
    </div>
    <div style="display: flex; margin-left: 10px">
      <div>请选择你要发布的地方</div>
      <div class="choose">
        <select v-model="id">
          <option :value="jrtt">今日头条</option>
          <option :value="essc">二手市场</option>
          <option :value="hljy">婚恋交友</option>
          <option :value="zxzs">装修装饰</option>
          <option :value="mrtt">明日头条</option>
          <option :value="llsc">劳力市场</option>
        </select>
      </div>
    </div>
    <div class="pubcontent">
      <textarea
        style="word-break: break-all"
        v-model="content"
        placeholder="请输入内容"
      ></textarea>
    </div>
    <div class="tu">
      <van-uploader :after-read="afterRead" />
    </div>
    <div class="btn">
      <van-button type="primary" @click="submit">确定</van-button>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { Button, Notify, Uploader  } from "vant";
export default {
  data() {
    return {
      title: "",
      content: "",
      imageSrc: ['C:\Users\fei\Desktop\前端vscode\vue\toutiao\src\assets\hg.jpg'],
      id: "",
      jrtt: ["610128169b33ed00018ed5df", "今日头条"],
      essc: ["609e99e0f2e56f000175151f", "二手市场"],
      hljy: ["60e83b83a30ce80001c087a0", "婚恋交友"],
      zxzs: ["60e83b96827eca0001ba827c", "装修装饰"],
      mrtt: ["60e83bd9f6a3980001a11be5", "明日头条"],
      llsc: ["61863e3519452d00017f1a8c", "劳力市场"],
      a:123
    };
  },
  components: {
    [Button.name]: Button,
    [Notify.name]: Notify,
    [Uploader.name]:Uploader 
  },
  methods: {
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      console.log(file);
    },
    submit() {
      // console.dir(this.id[0]);
      axios({
        method: "post",
        url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_article",
        data: {
          title: this.title,
          content: this.content,
          cate_id: this.id[0],
          cate_name: this.id[1],
          // author: this.a,
          author_id: localStorage.getItem("uid"),
          imageSrc: this.imageSrc,
        },
      }).then((res) => {
        // console.log(res.data);
        if (res.data.msg === "成功") {
          Notify({
            type: "primary",
            message: `发布成功`,
            duration: 1000,
          });
          this.$router.push("/index");
        } else {
          Notify({
            type: "warning",
            message: res.data.msg,
            duration: 1000,
          });
        }
        // console.log(this.imageSrc);
      });
    },
  },
};
</script>

<style scoped>
.all {
  background-color: rgb(245, 247, 249);
  height: 620px;
  /* border: 1px solid red; */
}
.pubhead {
  /* border: 1px solid red; */
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: white;
  background-color: rgb(7, 193, 96);
}
.pubtitle {
  /* border: 1px solid red; */
  height: 40px;
  width: 99%;
  margin: 10px 0;
  overflow: hidden;
}
.pubtitle input {
  height: 100%;
  width: 99%;
  border: none;
  padding-left: 10px;
}
.pubcontent {
  /* border: 1px solid red; */
  height: 100px;
  margin: 10px 0;
}
.pubcontent textarea {
  width: 360px;
  border: none;
  overflow: visible;
  word-break: break-all;
  height: 50px;
  padding-left: 10px;
  padding-top: 10px;
  padding-bottom: 50px;
  padding: 10px 0px 50px 10px;
}
.tu {
  /* border: 1px solid red; */
  padding: 25px;
  width: 40px;
}
.tu svg {
  /* border:  1px solid red; */
  width: 30px;
  height: 30px;
}
.btn {
  /* border:  1px solid red; */
  text-align: center;
}
.btn button {
  width: 300px;
}
</style>